@import url(https://fonts.googleapis.com/css?family=Montserrat+Subrayada);
@import url(https://fonts.googleapis.com/css?family=Denk+One);
@import url(https://fonts.googleapis.com/css?family=Montserrat+Subrayada);
@import url(https://fonts.googleapis.com/css?family=Denk+One);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wrap{
	display: grid;
	grid-template-rows: repeat(5, 1fr);
	grid-template-columns: repeat(5, 1fr);
	grid-gap: 2em 1em;
	place-items: center;
}

.body{
	position: absolute;
	width: 100vw;
	height: 100%;
	background-color: black;
	overflow-x: hidden;
	padding: 4rem 5rem 4rem 5rem;
	text-align: center;
}

h2{
	line-height: 5em;
	font-family: 'Denk One', sans-serif;
	text-shadow: -1px -1px 0px rgba(255,255,255,0.3), 1px 1px 0px rgba(0,0,0,0.8);
	color: #ffffff;
	opacity: 0.4;
	text-align: center;
} 

@media screen and (min-width: 1150px) {
	.wrap{
		margin-left: 10em;
		margin-right: 10em;
	}
}

@media (min-width: 100px) and (max-width: 767px) {
	.wrap{
		grid-gap: 1em 0.5em;
	}
	.body{
		padding-top: 3em;
	}
}



img{
	width: 5em;
	height: 8em;
}

img:hover{
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
	transition: ease-in-out;
}

img#verso{ 
	background: url(/Memory_Game/static/media/399772.c9ef92fe.jpg);
	background-size: auto 100%;
	background-position: center;
	display: block;
 box-sizing: border-box;
  	padding-right: 5em; 
}

#recto{
	box-shadow: 0px 0px 15px 1px white;
}

//to form the marble solitaire shape
img:nth-last-child(1){
grid-column-end: -2 ;
}
img:nth-last-child(19){
grid-column-end: -2;    
}
img:nth-last-child(18){
grid-column-start: 1;    
}
img:nth-last-child(3){
grid-column-start: 2;   
}
img:nth-last-child(21){
grid-column-start: 2;   
}


@media (min-width: 600px) and (max-width: 767px) {
  
  img{
  	width: 3em;
	height: 4em;
  }
  img#verso{
  	padding-right: 3em;
  } 
}

@media (min-width: 100px) and (max-width: 600px) {
  
  img{
  	width: 2em;
	height: 3em;
  }
  img#verso{
  	padding-right: 2em;
  } 
}

.navcontainer{
	background-image: linear-gradient(#1a1a1a 80%, black); 
	height: 80px;
	width: 100vw;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	box-shadow: 0px 0px 15px 1px white;
}

.item{
	color: #ffde72;
	-webkit-flex-shrink: 0;
	        flex-shrink: 0;
	padding: 1px;
	margin-left: 20px;
	margin-top: 10px; 
}

h1.item {
	font-family: 'Montserrat Subrayada', sans-serif;
}

h2.item{
	font-family: 'Denk One', sans-serif;
	text-shadow: -1px -1px 0px rgba(255,255,255,0.3), 1px 1px 0px rgba(0,0,0,0.8);
	color: #ffffff;
	opacity: 0.4;
}

@media all and (max-width: 500px){

	.navcontainer{
		-webkit-justify-content: center;
		        justify-content: center;
		-webkit-flex-wrap: wrap;
		        flex-wrap: wrap;
	}
}
.modal {
	height: 25em;
	font-size: 12px;
	background-color: black;
}
.container > button {
	margin-top: 2em;
	font-family: 'Denk One', sans-serif;	
}

.modal > .header {
  width: 100%;
  border-bottom: 1px solid white;
  font-size: 18px;
  text-align: center;
  padding: 5px;
  font-family: 'Montserrat Subrayada', sans-serif;
  color: white;
}
.modal > .content {
	position: relative;
	width: 100%;
	padding: 10px 5px;
}
.content > img {
	padding: 10px;
	height: 10em;
	width: auto;
}
.content > p {
	font-size: 1.2em;
	font-family: 'Denk One', sans-serif;
	color: white;
}

.content > img:hover{
	-webkit-transform: scale(1);
	        transform: scale(1);
}

.modal > .actions {
  width: 100%;
  padding: 0px 5px;
  margin: auto;
  text-align: center;
  font-family: 'Denk One', sans-serif;
}
.modal > .close {
  cursor: pointer;
  position: absolute;
  display: block;
  padding: 2px 5px;
  line-height: 20px;
  right: -0.5em;
  top: -0.5em;
  font-size: 24px;
  background: black;
  border-radius: 18px;
  border: 1px solid #cfcece;
  color: white;
}

.bars{
	position: absolute;
	height: 10em;
	width: 100%;
	left:0;
	right: 0;
	margin-left: auto;
	margin-right: auto;
	z-index: 1;
	-webkit-animation: inprison 3s;
	        animation: inprison 3s;
}

@-webkit-keyframes inprison {
	from {
		opacity: 0%;
	}
	80%{
		opacity: 50%;
	}

	100%{
		opacity: 100%;
	}
}

@keyframes inprison {
	from {
		opacity: 0%;
	}
	80%{
		opacity: 50%;
	}

	100%{
		opacity: 100%;
	}
}

@media (min-width: 100px) and (max-width: 767px) {
	
	.content > img{
		height:7em;
		width: auto;
	}

}
